body {
  height: 100vh;
  background: #222 url("https://source.unsplash.com/1600x900/?landscape");
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 100%;
  display: flex;
}

.card {
  -webkit-backdrop-filter: blur(11.5px);
  backdrop-filter: blur(11.5px);
  -webkit-backdrop-filter: blur(11.5px);
  color: #fff;
  width: 100%;
  max-width: 420px;
  background: rgba(0, 0, 0, .3);
  border: 1px solid rgba(255, 255, 255, .18);
  border-radius: 30px;
  margin: 1em;
  padding: 2em;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(31, 38, 135, .37);
}

.search {
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  height: 44px;
  width: 44px;
  color: #fff;
  cursor: pointer;
  background: rgba(255, 255, 255, .25);
  border: none;
  border-radius: 50%;
  outline: none;
  margin: .5em;
  transition: all .2s ease-in-out;
}

input.search-bar {
  color: #fff;
  width: calc(100% - 100px);
  background: rgba(0, 0, 0, .3);
  border: none;
  border-radius: 24px;
  outline: none;
  padding: .4em 1em;
  font-family: inherit;
  font-size: 105%;
}

button:hover {
  background: rgba(124, 124, 124, .42);
}

h1.temp {
  margin: 0 0 .4em;
}

.flex {
  align-items: center;
  display: flex;
}

.description {
  text-transform: capitalize;
  margin-left: 8px;
}

.weather.loading {
  visibility: hidden;
  max-height: 20px;
  position: relative;
}

.weather.loading:after {
  visibility: visible;
  content: "Loading...";
  color: #fff;
  position: absolute;
  top: 0;
  left: 20px;
}

.js-tilt-glare {
  border-radius: 10px;
}

a {
  color: inherit;
  justify-content: center;
  align-items: center;
  margin-top: 7%;
  display: flex;
}

